    export default {
        name: 'mch-detail',
	    data() {
            return {
                detail: {
                    user: {
                        nickname: '',
                    },
                    realname: '',
                    mobile: '',
                    wechat: '',
                    scope: '',
                    districts: '',
                    address: '',
                    cat_name: '',
                    service_mobile: '',
                    form_data: [],
                },
                model: false,
                modelType: 0,
                reasonRefusal: '',
                transfer_rate: null,
                pageShow: false,
                showForm: false,
            }
	    },
	    onLoad(options) { this.$commonLoad.onload(options);
            this.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            this.$request({
                url: this.$api.app_admin.review_detail,
                data: {
                    id: options.id
                }
            }).then(response => {
                this.$hideLoading();
                if (response.code === 0) {
                    this.pageShow = true;
                    this.detail = response.data.detail;
                    for(let item of this.detail.form_data) {
                    	if(item.value) {
                    		this.showForm = true;
                    	}
                    }
                }
            }).catch(() => {
                this.$hideLoading();
            });
	    },
	    methods: {
            getTypeof(data) {
                return typeof(data);
            },
            fail() {
                this.model = true;
                this.modelType = 1;
            },
		    by() {
                this.model = true;
                this.modelType = 2;
            },
            cancel() {
                this.model = false;
                this.modelType = 0;
            },
            confirm() {
                if (this.modelType === 2) {
                    this.detail.transfer_rate = this.transfer_rate;
                    this.$request({
	                    url: this.$api.app_admin.review_switch,
	                    method: 'post',
	                    data: {
	                        type: 1,
		                    status: 1,
		                    form: JSON.stringify(this.detail),
                            user_id: this.detail.id
	                    }
                    }).then(response => {
                        if (response.code === 0) {
                            uni.navigateBack();
                            this.model = false;
                        } else if (response.code === 1) {
							uni.showToast({
								title: response.msg
							})
                        }
                    })
                } else if (this.modelType === 1) {
                    this.detail.review_remark = this.reasonRefusal;
                    this.$request({
                        url: this.$api.app_admin.review_switch,
                        method: 'post',
                        data: {
                            type: 1,
                            status: 2,
                            form: JSON.stringify(this.detail),
                            user_id: this.detail.id
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            uni.navigateBack();
                            this.model = false;
                        } else if (response.code === 1) {
                            uni.showToast({
                                title: response.msg
                            })
                        }
                    });
                }
            },
            preview(data) {
                uni.previewImage({
                    urls: [data]
                });
            },
            onInput(data) {
                if (Number(data.detail.value) > 1000) {
                    setTimeout(() => {
                        this.transfer_rate = 1000;
                    });
                }
            }
	    }
    }
